// Number formatting utilities

export const fmt = (n) =>
  n == null ? "" :
  n < 0 ? `-$${Math.abs(n).toLocaleString("en-US", { maximumFractionDigits: 0 })}` :
  `$${n.toLocaleString("en-US", { maximumFractionDigits: 0 })}`;

export const fmtK = (n) =>
  n == null ? "" :
  n >= 1000 ? `$${(n / 1000).toFixed(0)}K` :
  fmt(n);

export const fmtPct = (n) =>
  n == null ? "" :
  `${(n * 100).toFixed(1)}%`;

export const fmtNum = (n) =>
  n == null ? "" :
  n.toLocaleString("en-US", { maximumFractionDigits: 0 });

export const pctColor = (v, good = "up") => {
  if (v == null) return "text-slate-500";
  if (good === "up") return v > 0 ? "text-green-500" : v < 0 ? "text-red-500" : "text-slate-500";
  return v < 0 ? "text-green-500" : v > 0 ? "text-red-500" : "text-slate-500";
};

// Legacy color function for charts (returns hex)
export const pctColorHex = (v, good = "up") => {
  if (v == null) return "#64748b";
  if (good === "up") return v > 0 ? "#22c55e" : v < 0 ? "#ef4444" : "#64748b";
  return v < 0 ? "#22c55e" : v > 0 ? "#ef4444" : "#64748b";
};

// Color utilities for conditional styling
export const getMarginColor = (margin) => {
  if (margin < 0) return "text-red-500";
  if (margin < 0.05) return "text-amber-500";
  return "text-green-500";
};

export const getTacosColor = (tacos) => {
  if (tacos > 0.20) return "text-red-500";
  if (tacos > 0.15) return "text-amber-500";
  return "text-green-500";
};

export const getOrganicColor = (organicPct) => {
  if (organicPct < 0.30) return "text-red-500";
  if (organicPct < 0.50) return "text-amber-500";
  return "text-green-500";
};
