import { pctColorHex } from '../../utils/formatters';

export default function KPICard({ label, value, sub, change, goodDir }) {
  const changeColor = change != null ? pctColorHex(change, goodDir) : null;

  return (
    <div className="bg-slate-950 rounded-xl p-5 border border-slate-800 flex-1 min-w-[180px]">
      <div className="text-[11px] uppercase tracking-wider text-slate-500 mb-2 font-semibold">
        {label}
      </div>
      <div className="text-[28px] font-extrabold text-slate-100 font-mono">
        {value}
      </div>
      <div className="flex items-center gap-1.5 mt-1.5">
        {change != null && (
          <span
            className="text-xs font-bold font-mono"
            style={{ color: changeColor }}
          >
            {change > 0 ? "+" : ""}{(change * 100).toFixed(1)}% MoM
          </span>
        )}
        {sub && (
          <span className="text-[11px] text-slate-500">{sub}</span>
        )}
      </div>
    </div>
  );
}
