import { fmt } from '../../utils/formatters';

export default function CTooltip({ active, payload, label, formatter }) {
  if (!active || !payload?.length) return null;

  return (
    <div className="bg-slate-800 border border-slate-700 rounded-lg px-3.5 py-2.5 text-xs text-slate-200">
      <div className="font-bold mb-1 text-slate-400">{label}</div>
      {payload.map((p, i) => (
        <div key={i} className="flex gap-2 items-center mb-0.5">
          <span
            className="w-2 h-2 rounded-sm inline-block"
            style={{ background: p.color }}
          />
          <span>{p.name}:</span>
          <span className="font-semibold text-white">
            {formatter ? formatter(p.value) : fmt(p.value)}
          </span>
        </div>
      ))}
    </div>
  );
}
