import { useState } from 'react';
import { ResponsiveContainer, ComposedChart, LineChart, Bar, Line, Area, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts';
import { DATA } from '../../utils/dataUtils';
import { BRAND_COLORS } from '../../data/rawData';
import { ChartCard, CTooltip } from '../ui';

export default function BrandDeepDive() {
  const allBrands = [...new Set(DATA.flatMap(d => d.brands.map(b => b.b)))];
  const [selBrand, setSelBrand] = useState("Fomin");

  const brandTrend = DATA.map(d => {
    const b = d.brands.find(x => x.b === selBrand);
    if (!b) return null;
    return {
      period: d.period,
      revenue: b.rev,
      profit: b.gp,
      margin: b.gm * 100,
      tacos: b.tacos * 100,
      units: b.units,
      revPerUnit: b.revPerUnit,
      profitPerUnit: b.profitPerUnit,
      organicPct: b.organicRevPct * 100,
      adSpend: b.totalAd,
      storage: (b.storage || 0) + (b.lts || 0),
      fba: b.fba || 0
    };
  }).filter(Boolean);

  return (
    <div>
      <div className="flex gap-2 mb-5 flex-wrap">
        {allBrands.map(b => (
          <button
            key={b}
            onClick={() => setSelBrand(b)}
            className={`py-2 px-4 rounded-lg border-none text-xs font-semibold cursor-pointer transition-all ${
              selBrand === b ? 'text-white' : 'bg-slate-800 text-slate-400'
            }`}
            style={selBrand === b ? { background: BRAND_COLORS[b] || "#3b82f6" } : {}}
          >
            {b}
          </button>
        ))}
      </div>

      <div className="flex gap-4 flex-wrap mb-5">
        <ChartCard title="REVENUE & PROFIT" className="flex-1 min-w-[400px]">
          <ResponsiveContainer width="100%" height={240}>
            <ComposedChart data={brandTrend}>
              <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
              <XAxis dataKey="period" tick={{ fontSize: 9, fill: "#64748b" }} interval={2} />
              <YAxis tick={{ fontSize: 10, fill: "#64748b" }} tickFormatter={v => `$${(v / 1000).toFixed(0)}K`} />
              <Tooltip content={<CTooltip />} />
              <Area dataKey="revenue" fill={`${BRAND_COLORS[selBrand] || "#3b82f6"}22`} stroke={BRAND_COLORS[selBrand] || "#3b82f6"} strokeWidth={2} name="Revenue" />
              <Bar dataKey="profit" fill="#22c55e" radius={[2, 2, 0, 0]} name="Profit" barSize={14} />
            </ComposedChart>
          </ResponsiveContainer>
        </ChartCard>

        <ChartCard title="MARGIN vs TACoS vs ORGANIC %" className="flex-1 min-w-[350px]">
          <ResponsiveContainer width="100%" height={240}>
            <LineChart data={brandTrend}>
              <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
              <XAxis dataKey="period" tick={{ fontSize: 9, fill: "#64748b" }} interval={2} />
              <YAxis tick={{ fontSize: 10, fill: "#64748b" }} tickFormatter={v => `${v}%`} />
              <Tooltip content={<CTooltip formatter={v => `${v.toFixed(1)}%`} />} />
              <Line dataKey="margin" stroke="#22c55e" strokeWidth={2} dot={false} name="Margin %" />
              <Line dataKey="tacos" stroke="#ef4444" strokeWidth={2} dot={false} name="TACoS %" />
              <Line dataKey="organicPct" stroke="#3b82f6" strokeWidth={2} dot={false} name="Organic %" />
            </LineChart>
          </ResponsiveContainer>
        </ChartCard>
      </div>

      <div className="flex gap-4 flex-wrap">
        <ChartCard title="UNIT ECONOMICS" className="flex-1 min-w-[350px]">
          <ResponsiveContainer width="100%" height={220}>
            <ComposedChart data={brandTrend}>
              <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
              <XAxis dataKey="period" tick={{ fontSize: 9, fill: "#64748b" }} interval={2} />
              <YAxis tick={{ fontSize: 10, fill: "#64748b" }} tickFormatter={v => `$${v.toFixed(0)}`} />
              <Tooltip content={<CTooltip formatter={v => `$${v.toFixed(2)}`} />} />
              <Bar dataKey="revPerUnit" fill="#3b82f6" radius={[2, 2, 0, 0]} name="Rev/Unit" barSize={12} />
              <Line dataKey="profitPerUnit" stroke="#22c55e" strokeWidth={2} dot={false} name="Profit/Unit" />
            </ComposedChart>
          </ResponsiveContainer>
        </ChartCard>

        <ChartCard title="AD SPEND vs STORAGE COSTS" className="flex-1 min-w-[350px]">
          <ResponsiveContainer width="100%" height={220}>
            <ComposedChart data={brandTrend}>
              <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
              <XAxis dataKey="period" tick={{ fontSize: 9, fill: "#64748b" }} interval={2} />
              <YAxis tick={{ fontSize: 10, fill: "#64748b" }} tickFormatter={v => `$${(v / 1000).toFixed(0)}K`} />
              <Tooltip content={<CTooltip />} />
              <Bar dataKey="adSpend" fill="#ef4444" radius={[2, 2, 0, 0]} name="Ad Spend" barSize={12} />
              <Bar dataKey="storage" fill="#f59e0b" radius={[2, 2, 0, 0]} name="Storage+LTS" barSize={12} />
            </ComposedChart>
          </ResponsiveContainer>
        </ChartCard>
      </div>
    </div>
  );
}
