export default function DataTable({ headers, children }) {
  return (
    <div className="overflow-x-auto">
      <table className="w-full border-collapse text-xs">
        <thead>
          <tr className="border-b-2 border-slate-800">
            {headers.map((h, i) => (
              <th
                key={h}
                className={`py-2.5 px-3 text-slate-500 font-semibold text-[10px] uppercase tracking-wide ${
                  i === 0 ? 'text-left' : 'text-right'
                }`}
              >
                {h}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>{children}</tbody>
      </table>
    </div>
  );
}

export function TableRow({ children, highlight = false }) {
  return (
    <tr className={`border-b border-slate-800 ${highlight ? 'border-t-2 border-t-slate-700 font-bold' : ''}`}>
      {children}
    </tr>
  );
}

export function TableCell({ children, align = "right", className = "" }) {
  return (
    <td className={`py-2.5 px-3 font-mono ${align === "left" ? "text-left" : "text-right"} ${className}`}>
      {children}
    </td>
  );
}
