import { ResponsiveContainer, ComposedChart, LineChart, BarChart, Bar, Line, Area, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts';
import { DATA } from '../../utils/dataUtils';
import { BRAND_COLORS } from '../../data/rawData';
import { fmt, fmtK, fmtPct, fmtNum, pctColorHex } from '../../utils/formatters';
import { KPICard, ChartCard, CTooltip, DataTable, TableRow, TableCell } from '../ui';

export default function ExecSummary() {
  const latest = DATA[DATA.length - 1];
  const prev = DATA[DATA.length - 2];
  const yoyIdx = DATA.length >= 13 ? DATA.length - 13 : 0;
  const yoy = DATA[yoyIdx];

  const revChange = prev.totalRev ? (latest.totalRev - prev.totalRev) / prev.totalRev : 0;
  const gpChange = prev.totalGP ? (latest.totalGP - prev.totalGP) / prev.totalGP : 0;
  const yoyRevChange = yoy.totalRev ? (latest.totalRev - yoy.totalRev) / yoy.totalRev : 0;

  // Trend data
  const trendData = DATA.map(d => ({
    period: d.period,
    revenue: d.totalRev,
    profit: d.totalGP,
    margin: d.margin * 100,
    tacos: d.tacos * 100,
    units: d.totalUnits
  }));

  // Alerts
  const alerts = [];
  latest.brands.forEach(b => {
    if (b.gm < 0) alerts.push({ type: "critical", msg: `${b.b} is LOSING money (${fmtPct(b.gm)} margin)` });
    else if (b.gm < 0.05) alerts.push({ type: "warning", msg: `${b.b} margin critically thin at ${fmtPct(b.gm)}` });
    if (b.tacos > 0.20) alerts.push({ type: "warning", msg: `${b.b} TACoS at ${fmtPct(b.tacos)}  ad dependency too high` });
    if (b.refRate > 0.08) alerts.push({ type: "warning", msg: `${b.b} refund rate at ${fmtPct(b.refRate)}` });
    if (b.lts > b.rev * 0.02) alerts.push({ type: "warning", msg: `${b.b} long-term storage fees eating ${fmtPct(b.lts / b.rev)} of revenue` });
  });
  if (latest.margin < 0.10) alerts.push({ type: "critical", msg: `Portfolio margin dropped to ${fmtPct(latest.margin)}  below 10% threshold` });

  const headers = ["Brand", "Revenue", "Units", "Gross Profit", "Margin", "TACoS", "ACoS", "Organic %", "Rev/Unit", "Profit/Unit", "COGS %", "Fee %", "Ad %"];

  return (
    <div>
      {/* Alerts */}
      {alerts.length > 0 && (
        <div className="mb-6">
          <div className="text-[13px] font-bold text-red-500 uppercase tracking-wide mb-2.5">
            Action Required  {latest.period}
          </div>
          <div className="flex flex-col gap-1.5">
            {alerts.map((a, i) => (
              <div
                key={i}
                className={`py-2.5 px-4 rounded-lg text-[13px] font-medium border ${
                  a.type === "critical"
                    ? "bg-red-500/10 border-red-500/30 text-red-300"
                    : "bg-amber-500/10 border-amber-500/30 text-amber-300"
                }`}
              >
                {a.type === "critical" ? "" : ""} {a.msg}
              </div>
            ))}
          </div>
        </div>
      )}

      {/* KPI Cards */}
      <div className="flex gap-3 mb-6 flex-wrap">
        <KPICard
          label="Revenue"
          value={fmtK(latest.totalRev)}
          change={revChange}
          goodDir="up"
          sub={`YoY: ${yoyRevChange > 0 ? "+" : ""}${(yoyRevChange * 100).toFixed(0)}%`}
        />
        <KPICard label="Gross Profit" value={fmtK(latest.totalGP)} change={gpChange} goodDir="up" />
        <KPICard label="Margin" value={fmtPct(latest.margin)} change={latest.margin - prev.margin} goodDir="up" />
        <KPICard label="TACoS" value={fmtPct(latest.tacos)} change={latest.tacos - prev.tacos} goodDir="down" />
        <KPICard
          label="Units"
          value={fmtNum(latest.totalUnits)}
          change={prev.totalUnits ? (latest.totalUnits - prev.totalUnits) / prev.totalUnits : 0}
          goodDir="up"
        />
      </div>

      {/* Revenue + Profit Trend */}
      <div className="flex gap-4 mb-6 flex-wrap">
        <ChartCard title="REVENUE & GROSS PROFIT TREND" className="flex-[2] min-w-[400px]">
          <ResponsiveContainer width="100%" height={260}>
            <ComposedChart data={trendData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
              <XAxis dataKey="period" tick={{ fontSize: 10, fill: "#64748b" }} interval={1} />
              <YAxis tick={{ fontSize: 10, fill: "#64748b" }} tickFormatter={v => `$${(v / 1000).toFixed(0)}K`} />
              <Tooltip content={<CTooltip />} />
              <Area dataKey="revenue" fill="rgba(59,130,246,0.1)" stroke="#3b82f6" strokeWidth={2} name="Revenue" />
              <Bar dataKey="profit" fill="#22c55e" radius={[3, 3, 0, 0]} name="Gross Profit" barSize={16} />
            </ComposedChart>
          </ResponsiveContainer>
        </ChartCard>

        <ChartCard title="MARGIN vs TACoS" className="flex-1 min-w-[280px]">
          <ResponsiveContainer width="100%" height={260}>
            <LineChart data={trendData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
              <XAxis dataKey="period" tick={{ fontSize: 10, fill: "#64748b" }} interval={2} />
              <YAxis tick={{ fontSize: 10, fill: "#64748b" }} tickFormatter={v => `${v}%`} domain={[0, 'auto']} />
              <Tooltip content={<CTooltip formatter={v => `${v.toFixed(1)}%`} />} />
              <Line dataKey="margin" stroke="#22c55e" strokeWidth={2} dot={false} name="Margin %" />
              <Line dataKey="tacos" stroke="#ef4444" strokeWidth={2} dot={false} name="TACoS %" />
            </LineChart>
          </ResponsiveContainer>
        </ChartCard>
      </div>

      {/* Brand Scorecards */}
      <div className="text-[13px] font-bold text-slate-400 mb-3">BRAND SCORECARDS  {latest.period}</div>
      <DataTable headers={headers}>
        {latest.brands.map(b => (
          <TableRow key={b.b}>
            <TableCell align="left" className="font-bold" style={{ color: BRAND_COLORS[b.b] || "#e2e8f0" }}>
              <span className="inline-block w-2 h-2 rounded-sm mr-2" style={{ background: BRAND_COLORS[b.b] || "#64748b" }} />
              {b.b}
            </TableCell>
            <TableCell className="text-slate-100">{fmtK(b.rev)}</TableCell>
            <TableCell className="text-slate-300">{fmtNum(b.units)}</TableCell>
            <TableCell className={`font-semibold ${b.gp < 0 ? "text-red-500" : "text-green-500"}`}>{fmtK(b.gp)}</TableCell>
            <TableCell className={`font-bold ${b.gm < 0 ? "text-red-500" : b.gm < 0.05 ? "text-amber-500" : "text-green-500"}`}>{fmtPct(b.gm)}</TableCell>
            <TableCell className={b.tacos > 0.20 ? "text-red-500" : b.tacos > 0.15 ? "text-amber-500" : "text-green-500"}>{fmtPct(b.tacos)}</TableCell>
            <TableCell className="text-slate-300">{fmtPct(b.acos)}</TableCell>
            <TableCell className={b.organicRevPct < 0.30 ? "text-red-500" : b.organicRevPct < 0.50 ? "text-amber-500" : "text-green-500"}>{fmtPct(b.organicRevPct)}</TableCell>
            <TableCell className="text-slate-300">{fmt(b.revPerUnit)}</TableCell>
            <TableCell className={b.profitPerUnit < 0 ? "text-red-500" : "text-slate-300"}>{fmt(b.profitPerUnit)}</TableCell>
            <TableCell className="text-slate-400">{fmtPct(b.cogsRate)}</TableCell>
            <TableCell className="text-slate-400">{fmtPct(b.feeRate)}</TableCell>
            <TableCell className="text-slate-400">{fmtPct(b.adRate)}</TableCell>
          </TableRow>
        ))}
        <TableRow highlight>
          <TableCell align="left" className="text-slate-100">TOTAL</TableCell>
          <TableCell className="text-slate-100">{fmtK(latest.totalRev)}</TableCell>
          <TableCell className="text-slate-100">{fmtNum(latest.totalUnits)}</TableCell>
          <TableCell className="text-green-500">{fmtK(latest.totalGP)}</TableCell>
          <TableCell className="text-green-500">{fmtPct(latest.margin)}</TableCell>
          <TableCell>{fmtPct(latest.tacos)}</TableCell>
          <TableCell colSpan={7}></TableCell>
        </TableRow>
      </DataTable>
    </div>
  );
}
